<template>
  <MainPage :lesson-num="lessonNum" :lesson-name="lessonName" :section-name="extraName" :previous-section="previousSection" :next-section="nextSection">
    <CollapsiblePane v-if="intro" name="Introduction" extra-class="section" :start-collapsed="startCollapsed">
      <div class="inner-text lesson-intro">
        <p v-html="intro"></p>
      </div>
    </CollapsiblePane>
    <section v-if="showToc" id="toc">
      <div id="toc-title">Table of Contents</div>
      <ol id="toc-nav">
        <li
            v-for="(name, index) in toc"
            :key="index"
        ><a href="javascript:void(0)" v-on:click="tocLink(index + 1)" v-html="name"></a></li>
      </ol>
    </section>
    <LessonSection
      v-for="(section, index) in sections"
      :key="index"
      :right-col-width="section.rightColWidth"
      :start-collapsed="startCollapsed"
      :lessonNum="lessonNum"
      :sectionNum="section.number"
      :sectionName="section.name"
      :web="section.web"
      :book="section.book"
      :exam="section.exam"
      :intro="section.intro"
      :steps="section.steps"
      :examples="section.examples"
    />
  </MainPage>
</template>

<script>
import MainPage from '@/components/MainPage'
import LessonSection from "@/components/LessonSection";
import CollapsiblePane from "@/components/CollapsiblePane";

export default {
  name: 'App',
  components: {
    CollapsiblePane,
    LessonSection,
    MainPage,
  },
  methods: {
    tocLink(index) {
      const parts = window.location.hash.replace(/^#/, '').split('&');
      const found = parts.findIndex(value => value.match(/^\w*section\w*=\w*\d+\w*$/i))

      if(found !== -1) {
        parts[found] = 'section=' + index;
      } else {
        parts.push('section=' + index);
      }

      window.scrollTo(0, 0);
      window.location.hash = '#' + parts.join('&');
      window.location.reload();
    },
  },
  mounted() {
    // Replace \p (waits 5 seconds to allow mathjax to do its replacements first)
    window.setTimeout(() => {
      const matchingElementSet = document.evaluate("//*[text()[contains(.,'\\p')]]", document, null, XPathResult.ORDERED_NODE_ITERATOR_TYPE, null);
      const elements = [];

      let element;
      // eslint-disable-next-line no-cond-assign
      while(element = matchingElementSet.iterateNext()) {
        elements.push(element);
      }

      for(const element of elements) {
        const parts = element.innerHTML.split('\\p');
        let html = parts[0];

        for(let i = 1; i < parts.length; i++) {
          html += '<span class="invisible">' + parts[i] + '</span>';
        }

        element.innerHTML = html;
      }
    }, 5000);

    const visStack = [];
    window.addEventListener('keyup', event => {
      if(event.key === 'v') {
        for(const e of document.querySelectorAll('.invisible')) {
          if(e.style.visibility === 'hidden') {
            e.style.visibility = 'visible';

            // Maintain 100 elements of history
            visStack.push(e);

            if(visStack.length > 100) {
              visStack.shift();
            }

            break;
          }
        }
      }

      if(event.key === 'h') {
        if(visStack.length !== 0) {
          visStack.pop().style.visibility = 'hidden';
        }
      }
    });
  },
  data() {
    function replaceStuff(obj, changes) {
      for(const key in obj) {
        if(typeof obj[key] === 'object') {
          obj[key] = replaceStuff(obj[key], changes);
        } else if(typeof obj[key] === 'string') {
          for(const from in changes) {
            obj[key] = obj[key].replaceAll(from, changes[from]);
          }
        }
      }

      return obj;
    }

    // Set section numbers and TOC
    window.j.toc = [];
    for(let i = 0; i < window.j.sections.length; i++) {
      window.j.sections[i].number = i + 1;
      window.j.toc[i] = window.j.sections[i].name;
    }

    window.j.showToc = false;
    window.j.extraName = null;
    window.j.previousSection = null;
    window.j.nextSection = null;

    // Check if we're viewing a specific section
    const parts = window.location.hash.replace(/^#/, '').split('&');
    const found = parts.find(value => value.match(/^\w*section\w*=\w*\d+\w*$/i))

    if(found) {
      const section = Number(found.split('=')[1]);

      if(section < window.j.sections.length) {
        window.j.nextSection = section + 1;
      }

      if(section === 0) {
        window.j.sections = [];
        window.j.showToc = true;
        window.j.extraName = 'Introduction';
      } else {
        window.j.intro = null;
        window.j.sections = [window.j.sections[section - 1]];
        window.j.extraName = window.j.sections[0].name;
        window.j.previousSection = section - 1;
        console.log(section-1);
      }
    }

    return replaceStuff(window.j, {
      '<pli>': '<li class="invisible">',
      '</pli>': '</li>'
    });
  }
}
</script>

<style>
* {
  font-size: 1em;
  margin: 0;
}

#app {
  font-family: tex, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
</style>
