<template>
  <nav id="top-nav" style="display: flex">
    <span style="width: 16%"><a v-if="previousSection !== null" href="javascript:void(0)" v-on:click="previous">Previous Section</a></span>
    <span style="margin: auto"><span>Calc I / <a href="javascript:void{0}" v-on:click="intro">{{ lessonName }}</a></span><span v-if="sectionName"> / {{ sectionName }}</span></span>
    <span style="width: 16%"><a v-if="nextSection !== null" href="javascript:void(0)" v-on:click="next">Next Section</a></span>
  </nav>
  <header>
    <h1>Lecture {{ lessonNum }}: {{ lessonName }}</h1>
    <div id="buttons">
      <button id="color-toggle" v-on:click="toggleColors"></button>
      <button id="collapse-all" v-on:click="collapseAll">Collapse</button>
      <button id="expand-all" v-on:click="expandAll">Expand</button>
      <button id="hide-all" v-on:click="hideAll">Hide</button>
    </div>
  </header>
  <section class="background">
    <slot />
  </section>
</template>

<script>
export default {
  name: 'MainPage',
  props: {
    lessonNum: Number,
    lessonName: String,
    sectionName: {
      type: String,
      required: false,
    },
    previousSection: {
      type: Number,
      required: false,
    },
    nextSection: {
      type: Number,
      required: false,
    },
  },
  mounted() {
    document.querySelector('#color-toggle').innerText = window.location.hash.includes('nocolor') ? 'Enable color' : 'Disable color';
  },
  methods: {
    toggleColors() {
      document.querySelector('#color-toggle').disabled = true;

      const parts = window.location.hash.replace(/^#/, '').split('&');
      let foundNoColor = false;

      for(const key in parts) {
        const part = parts[key];
        if(part === 'nocolor') {
          delete parts[key];
          foundNoColor = true;
        }
      }

      if(!foundNoColor) {
        parts.push('nocolor');
      }

      window.location.hash = '#' + parts.filter(Boolean).join('&');
      window.location.reload();
    },
    intro() {
      const parts = window.location.hash.replace(/^#/, '').split('&');

      for(const key in parts) {
        if(parts[key].match(/^\w*section\w*=\w*\d+\w*$/i)) {
          parts[key] = 'section=0';
          break;
        }
      }

      window.location.hash = '#' + parts.filter(Boolean).join('&');
      window.location.reload();
    },
    next() {
      const parts = window.location.hash.replace(/^#/, '').split('&');

      for(const key in parts) {
        if(parts[key].match(/^\w*section\w*=\w*\d+\w*$/i)) {
          let index;
          [, index] = parts[key].split('=');
          parts[key] = 'section=' + (Number(index) + 1);
          break;
        }
      }

      window.location.hash = '#' + parts.filter(Boolean).join('&');
      window.location.reload();
    },
    previous() {
      const parts = window.location.hash.replace(/^#/, '').split('&');

      for(const key in parts) {
        if(parts[key].match(/^\w*section\w*=\w*\d+\w*$/i)) {
          let index;
          [, index] = parts[key].split('=');
          parts[key] = 'section=' + (Number(index) - 1);
          break;
        }
      }

      window.location.hash = '#' + parts.filter(Boolean).join('&');
      window.location.reload();
    },
    collapseAll() {
      document.querySelectorAll('.collapsible').forEach(el => el.dataset.collapsed = 'true');
    },
    expandAll() {
      document.querySelectorAll('.collapsible').forEach(el => el.dataset.collapsed = 'false');
    },
    hideAll() {
      document.querySelectorAll('.invisible').forEach(el => el.style.visibility = 'hidden');
    },
  }
}
</script>

