<template>
  <section ref="section" :class="'collapsible'" v-bind:data-collapsed="collapsed" :style="{ 'background-color': backgroundColor }">
    <div :class="'section-div ' + extraClass" style="display: flex"><div style="flex-grow: 1"><h2 :class="'section-name'" v-on:click="onClick" v-html="'<span class=\'arrow\'></span> ' + name"></h2></div><div class="collapsible-buttons" style="padding: 1rem"><button v-on:click="hideChildren">Hide</button><button v-on:click="showChildren">Show</button></div></div>
    <section class="contents"><slot /></section>
  </section>
</template>

<script>
export default {
  name: "CollapsiblePane",
  props: {
    name: String,
    extraClass: {
      type: String,
      default: '',
    },
    backgroundColor: {
      type: String,
      default: "cornflowerblue",
    },
    startCollapsed: {
      type: Boolean,
      default: true,
      required: false,
    }
  },
  data: () => {
    return {
      collapsed: true,
    }
  },
  created() {
    this.collapsed = this.startCollapsed;
  },
  methods: {
    onClick() {
      this.collapsed = !this.collapsed;
    },
    hideChildren() {
      const {section} = this.$refs;
      section.querySelectorAll('.invisible').forEach(el => el.style.visibility = 'hidden');
    },
    showChildren() {
      const {section} = this.$refs;
      section.querySelectorAll('.invisible').forEach(el => el.style.visibility = 'visible');
    },
  },
}
</script>

<script setup>

</script>

